@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
	width: 100%;
	height: 100%;
	margin: 0px;
	padding: 0px;
	overflow-x: hidden;
}
.navbarcustom {
	background-color: rgb(250, 250, 250);
}

.navbarcustom2 {
	background-color: rgb(250, 250, 250);
	width: 600px;
	float: right;
}

.customlink {
	font-size: 1.5em;
	transition: 0.2s;
	padding: 2px;
	text-decoration: none;
	color: #e3a7ff !important;
	font-weight: 150;
}

.customlink:hover {
	color: black !important;
}

.customlink2 {
	font-size: 1.5em;
	transition: 0.2s;
	padding: 2px;
	text-decoration: none;
	color: white;
	font-weight: 150;
}

.customlink2:hover {
	color: black !important;
}
.logolink1 {
	background-image: url(/LawrenceBday/static/media/Triage-Logo.be59ba69.png);
	height: 83.33px;
	width: 116.915px;
	object-fit: cover;
	background-size: cover;
}

.logolink2 {
	background-image: url(/LawrenceBday/static/media/Triage-Logo-White.3bb017f8.png);
	height: 178.05px;
	width: 249.81px;
	object-fit: cover;
	background-size: cover;
	margin-left: 120px;
	margin-top: 10px;
}
.customh1 {
	font-family: 'Roboto', sans-serif;
	font-weight: 900;
	font-style: italic;
	font-size: 64px;
}
.roboto {
	font-family: 'Roboto', sans-serif;
}
.triage {
	font-family: 'Montserrat', sans-serif;
}

.titlecustom {
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
	font-style: none;
	color: #e3a7ff;
}

button {
	background-color: white;
	border: none;
	width: 200px;
	height: 60px;
}

.herocustom {
	width: 100%;
	height: 1000px;
}

.buttoncustom {
	transition: 0.3s;
}

.buttoncustom:hover {
	background-color: rgb(243, 243, 243);
	color: #c587e2;
}

.buttoncustom2 {
	transition: 0.3s;
	color: white;
	background-color: #e3a7ff;
}

.buttoncustom2:hover {
	background-color: #da9cf7;
	color: rgb(243, 243, 243);
}

.pcustom {
	font-family: 'Roboto', sans-serif;
	font-weight: 100;
	font-size: 18px;
	margin-top: 150px;
}

.testo {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #b694ff;
}

.footerp {
	color: white;
	font-weight: 300;
}

.center {
	margin: auto;
	width: 50%;
}

.boxcoffee {
	margin-top: 100px;
	display: flex;
	margin-right: 40px;
	margin-left: 40px;
	min-height: 350px;
	min-width: 300px;
	border: 1px black solid;
}

